import MacleJSBridge from 'src/bridges';
import { APINames } from 'src/shared';
import { GeneralInvokeOption } from 'src/types';

export interface ChooseImageOption extends Partial<GeneralInvokeOption> {
  count: number;
  sizeType: Array<String>;
  sourceType: Array<String>;
}

// 拍照或上传
export function chooseImage(option?: ChooseImageOption) {
  const fixedOption = Object.assign(
    {
      count: 9,
      sizeType: ['original', 'compressed'],
      sourceType: ['album', 'camera']
    },
    option
  );
  return MacleJSBridge.beforeInvoke(APINames.Native.CHOOSE_IMAGE, fixedOption);
}
