import MacleJSBridge from 'src/bridges';
import { IAnyStrObject } from 'src/types';
import { reportErrorLog } from '../utils';

export type NativeCallback = (res: any) => any;

// h5调用native原生方法
export function callNativeAPI(
  apiName: string,
  inputParams: IAnyStrObject = {},
  callback: NativeCallback
) {
  if (!callback || typeof callback !== 'function') {
    reportErrorLog(apiName, 'callback should be function!');
    return;
  }
  MacleJSBridge.invoke(apiName, inputParams, (invokeStatusCode, res) => {
    const isOk = Number(invokeStatusCode) === 0;
    if (!isOk) {
      reportErrorLog(apiName, `${res.errMsg}`);
      return;
    }
    callback(res);
  });
}
