import MacleJSBridge from 'src/bridges';
import { APINames } from 'src/shared';
import { advanceParamCheck } from '../../utils';

export interface ReportEventOption {
  eventId: string;
  invalidParamObj?: any;
}

const nonemptyParam = { nonempty: true }; // 非空参数

// 自定义埋点上报
export function reportEvent(option: ReportEventOption) {
  if (
    advanceParamCheck(
      APINames.Native.REPORT_EVENT,
      option,
      { eventId: '' },
      { eventId: nonemptyParam }
    )
  ) {
    return MacleJSBridge.notifyNative(APINames.Native.REPORT_EVENT, option);
  }
  return option.invalidParamObj;
}
