import MacleJSBridge from 'src/bridges';
import { APINames } from 'src/shared';
import { GeneralInvokeOption } from 'src/types';
import { logErrorAndRunFail, paramCheck } from '../../utils';

export interface NavigateToMiniProgramOptions
  extends Partial<GeneralInvokeOption> {
  appId: string;
  path?: string;
  params?: string;
  invalidParamObj?: any;
}

export function navigateToMiniProgram(option: NavigateToMiniProgramOptions) {
  if (!option.appId) {
    return logErrorAndRunFail(
      APINames.Native.NAVIGATE_TO_MINI_PROGRAM,
      option,
      'parameter error:parameter.appId should be String instead of Undefined.'
    );
  }
  if (
    paramCheck(APINames.Native.NAVIGATE_TO_MINI_PROGRAM, option, {
      appId: '',
      path: '',
      params: ''
    })
  ) {
    if (!option.path && option.params) {
      return logErrorAndRunFail(
        APINames.Native.NAVIGATE_TO_MINI_PROGRAM,
        option,
        'parameter error: there is params but no path.'
      );
    }
    return MacleJSBridge.beforeInvoke(
      APINames.Native.NAVIGATE_TO_MINI_PROGRAM,
      option
    );
  }
  return option.invalidParamObj;
}
