import MacleJSBridge from 'src/bridges';
import { APINames } from 'src/shared';
import { GeneralInvokeOption } from 'src/types';
import { logErrorAndRunFail, paramCheck, scopesCheck } from '../utils';

export interface GetAutoCodeOption extends Partial<GeneralInvokeOption> {
  scopes: Array<string>;
  invalidParamObj?: '';
}

// 获取登录凭证
export function getAuthCode(option: GetAutoCodeOption) {
  if (!option.scopes) {
    return logErrorAndRunFail(
      APINames.Native.GET_AUTH_CODE,
      option,
      'parameter error:parameter.scopes should be Array<string> instead of Undefined.'
    );
  }
  if (paramCheck(APINames.Native.GET_AUTH_CODE, option, { scopes: ['1'] })) {
    const invalidScopes = scopesCheck(option.scopes);
    if (invalidScopes.length > 0) {
      return logErrorAndRunFail(
        APINames.Native.GET_AUTH_CODE,
        option,
        `parameter error: invalid scopes ${invalidScopes}`
      );
    }
    return MacleJSBridge.beforeInvoke(APINames.Native.GET_AUTH_CODE, option);
  }
  return option.invalidParamObj;
}
