import MacleJSBridge from 'src/bridges';
import { APINames } from 'src/shared';
import { apiMethod } from './api-method';

function onBTDeviceFound() {
  MacleJSBridge.onBasicEvent(
    APINames.Native.ON_BLUETOOTH_DEVICE_FOUND,
    function (params: any) {
      apiMethod.emit(APINames.Native.ON_BLUETOOTH_DEVICE_FOUND, params);
    }
  );
}

export { onBTDeviceFound };
