import { APINames, getGlobal, TABBAR } from 'src/shared';
import MacleApiError from 'src/shared/api-error';
import { reporter } from 'src/shared/reporter';
import { IAnyStrObject, TabBar } from 'src/types';
import {
  NavigateToOption,
  RedirectToOption,
  SwitchTabOption,
  ReLaunchOption
} from './miniprogram';

/**
 * fix route
 * @param currentRoute 当前路由
 * @param toUrl 目标 url
 */
export function getRealRoute(
  currentRoute: string,
  toUrl: string,
  isHtml: boolean = true
): string {
  let fixedUrl = toUrl;

  if (isHtml) {
    fixedUrl = addHTMLSuffix(fixedUrl);
  }

  if (fixedUrl.startsWith('/')) {
    return fixedUrl.substr('/'.length);
  }

  if (fixedUrl.startsWith('./')) {
    return getRealRoute(currentRoute, fixedUrl.substr('./'.length), false);
  }

  // 移除 url 前面的 ../ 并记录其位置
  let index;
  let urlArrLength;
  const urlArr = fixedUrl.split('/');
  for (
    index = 0, urlArrLength = urlArr.length;
    index < urlArrLength && urlArr[index] === '..';
    index++
  );
  urlArr.splice(0, index);
  const pathArr = currentRoute.length > 0 ? currentRoute.split('/') : [];
  pathArr.splice(pathArr.length - index - 1, index + 1);
  return pathArr.concat(urlArr).join('/');
}

export function encodeUrlQuery(url: string): string {
  const urlArr = url.split('?');
  const [urlPath, queryUrl] = urlArr;
  if (!queryUrl) {
    return url;
  }
  const queryParams = queryUrl.split('&').reduce((res: IAnyStrObject, cur) => {
    if (typeof cur === 'string' && cur.length > 0) {
      const curArr = cur.split('=');
      const key = curArr[0];
      const value = curArr[1];
      res[key] = value;
    }
    return res;
  }, Object.create(null));
  const urlQueryArr = [];
  for (const i in queryParams) {
    urlQueryArr.push(i + '=' + encodeURIComponent(queryParams[i]));
  }
  return urlQueryArr.length > 0 ? urlPath + '?' + urlQueryArr.join('&') : url;
}

export function addHTMLSuffix(url: string): string {
  const urlArr = url.split('?');
  urlArr[0] += '.html';
  return urlArr[1] === undefined ? urlArr[0] : `${urlArr[0]}?${urlArr[1]}`;
}

export function checkUrl(
  apiName: string,
  options:
    | NavigateToOption
    | RedirectToOption
    | SwitchTabOption
    | ReLaunchOption
): boolean {
  const tabBar = getGlobal(TABBAR) as TabBar | null;
  if (tabBar === null) {
    return true;
  }

  if (options.url.startsWith('/')) {
    options.url = options.url.slice('/'.length);
  }

  // 目标 url 是否在 tabbar 中
  const inTabBar = ((url: string) =>
    tabBar?.list.find(e => url.indexOf(e.pagePath) === 0))(options.url);

  // navigateTo 和 redirectTo API 禁止跳转到 tabbar 页面
  if (
    apiName === APINames.MiniProgram.NAVIGATE_TO ||
    apiName === APINames.MiniProgram.REDIRECT_TO
  ) {
    if (inTabBar) {
      reportErrorLog(
        apiName,
        `${apiName}: can not ${apiName} to a tabbar page`
      );
      return false;
    }
  }

  // switchTab 禁止跳转到非 tabbar 页面
  if (apiName === APINames.MiniProgram.SWITCH_TAB) {
    if (!inTabBar) {
      reportErrorLog(
        apiName,
        `${apiName}: can not ${apiName} to a non-tabbar page`
      );
      return false;
    }
  }
  return true;
}

export function validateUrl(url: string) {
  return /^(http|https):\/\/.*/i.test(url);
}

export function getDataType(data: any) {
  return Object.prototype.toString.call(data).split(' ')[1].split(']')[0];
}

export function reportErrorLog(apiName: string, reason: string) {
  const err = new MacleApiError(apiName, reason);
  reporter.normalError({
    message: err.message,
    file: err.fileName,
    line: err.lineNumber,
    column: err.columnNumber
  });
  return err;
}

/**
 * 对象元素都转成字符串
 * @param {Object} obj
 * @returns
 */
export function convertObjectValueToString(obj: IAnyStrObject) {
  const newObj: IAnyStrObject = {};
  for (const key in obj) {
    switch (getDataType(obj[key])) {
      case 'String':
        newObj[key] = obj[key];
        break;
      case 'Number':
        newObj[key] = `${obj[key]}`;
        break;
      case 'Boolean':
        newObj[key] = `${obj[key]}`;
        break;
      default:
        newObj[key] = JSON.stringify(obj[key]);
        break;
    }
  }
  return newObj;
}

/**
 * Get请求情况下，将参数拼接到url后
 * @param {String} url
 * @param {Object} data
 * @returns 把查询参数加到url后的值
 */
export function addQueryStringToUrl(url: string, data: Object) {
  if (isString(url) && isObject(data) && Object.keys(data).length) {
    const host = url.split('?')[0];
    const query = url.split('?')[1];
    const oldParams: IAnyStrObject = {};
    if (query) {
      query.split('&').forEach(item => {
        if (item.length) {
          oldParams[item.split('=')[0]] = item.split('=')[1];
        }
      });
    }
    const encodeData: IAnyStrObject = {};
    for (const [key, value] of Object.entries(data)) {
      if (isObject(value)) {
        encodeData[key] = JSON.stringify(value);
      } else {
        encodeData[key] = value;
      }
    }
    return `${host}?${urlEncodeFormData(extend(oldParams, encodeData))}`;
  }
  return url;
}

export function isArray(data: any) {
  return getDataType(data) === 'Array';
}

export function isString(data: any) {
  return 'String' === getDataType(data);
}

export function isObject(data: any) {
  return 'Object' === getDataType(data);
}

/**
 * 将参数进行转码后拼接成String
 * @param {Object} data
 * @returns 参数进行转码后拼接成String
 */
export function urlEncodeFormData(data: any) {
  if (!isObject(data)) {
    return data;
  }
  const paramsArr = [];
  for (const key in data) {
    try {
      paramsArr.push(
        `${encodeURIComponent(key)}=${encodeURIComponent(data[key])}`
      );
    } catch (error) {
      paramsArr.push(`${key}=${data[key]}`);
    }
  }
  return paramsArr.join('&');
}

/**
 * 将obj合并到target对象
 * @param {IAnyStrObject} target
 * @param {IAnyStrObject} obj
 * @returns 合并后的对象
 */
function extend(target: IAnyStrObject, obj: IAnyStrObject) {
  for (const [key, value] of Object.entries(obj)) {
    target[key] = value;
  }
  return target;
}

// 统一构造能上报file、line和column的日志
export function logErrorAndRunFail(
  apiName: string,
  params: IAnyStrObject,
  reason: string
) {
  const err = reportErrorLog(apiName, reason);
  if (!params.success && !params.fail && !params.complete) {
    return new Promise((resolve, reject) => {
      params.fail = reject;
      params.fail({ errMsg: err.message });
    });
  }
  for (const callback of ['fail', 'complete']) {
    const emptyFn: Function = function () {};
    params[callback]?.({ errMsg: err.message }) || emptyFn;
  }
  return null;
}

export function paramCheck(
  apiName: string,
  params: IAnyStrObject,
  paramTpl: IAnyStrObject,
  name = 'parameter',
  required = false // 必填校验
) {
  let result = '';
  const pType = getDataType(params);
  const tplType = getDataType(paramTpl);
  if (pType !== tplType) {
    result = `${name} should be ${tplType} instead of ${pType};`;
    params.invalidParamObj = logErrorAndRunFail(apiName, params, result);
    return false;
  }
  if (tplType !== 'Object') {
    return true;
  }
  for (const [key, value] of Object.entries(paramTpl)) {
    if (Object.prototype.hasOwnProperty.call(params, key) || required) {
      const tplKeyType = getDataType(value);
      const pKeyType = getDataType(params[key]);
      if (tplKeyType !== pKeyType) {
        result += `${key} should be ${tplKeyType} instead of ${pKeyType};`;
      }
    }
  }
  if (!result) {
    return true;
  }
  params.invalidParamObj = logErrorAndRunFail(apiName, params, result);
  return false;
}

/**
 * 进阶版的参数校验，覆盖常见的通用校验场景
 */
export function advanceParamCheck(
  apiName: string,
  params: IAnyStrObject,
  paramTpl: IAnyStrObject,
  options: IAnyStrObject = {}, 
  name = 'parameter'
) {
  const finalParamTpl = paramTplPreProcess(params, paramTpl, options); // 参数模板的前处理
  // 检查基本类型
  if (!paramCheck(apiName, params, finalParamTpl, name, true)) {
    return false;
  }
  return paramPostCheck(apiName, params, finalParamTpl, options, name);
}

/**
 * api参数等值的前处理
 */
function paramTplPreProcess(
  params: IAnyStrObject,
  paramTpl: IAnyStrObject,
  options: IAnyStrObject = {}, 
) {
  const finalParamTpl = { ...paramTpl };
  for (let key in paramTpl) {
    // 可选参数，在未定义时不做任何相关校验
    if (params[key] === undefined && options[key]?.optional) {
      delete finalParamTpl[key];
      delete options[key];
    }
  }
  return finalParamTpl;
}

/**
 * 参数后校验处理，判断空值等复杂场景
 */
function paramPostCheck(
    apiName: string,
    params: IAnyStrObject,
    _paramTpl: IAnyStrObject,
    options: IAnyStrObject = {},
    name = 'parameter'
) {
  let result = '';

  for (let key in options) {
    const paramValue = params[key];
    const { nonempty } = options[key];
    // 非空校验
    if (nonempty) {
      const isEmptyArr = isArray(paramValue) && !paramValue.length;
      const isEmptyStr = isString(paramValue) && !paramValue.trim();
      if (isEmptyArr || isEmptyStr) {
        result += `parameter error: ${key} cannot be empty;`;
      }
    }
  }

  if (result) {
    params.invalidParamObj = logErrorAndRunFail(apiName, params, result);
    return false;
  }

  return true;
}


export function scopesCheck(scopes: Array<string>) {
  const validScopes = [
    'AUTH_BASE',
    'AUTH_USER',
    'SEND_MESSAGE',
    'USER_NICKNAME',
    'USER_NAME',
    'USER_LOGIN_ID',
    'PLAINTEXT_USER_LOGIN_ID',
    'HASH_LOGIN_ID',
    'USER_AVATAR',
    'USER_GENDER',
    'USER_BIRTHDAY',
    'USER_NATIONALITY',
    'USER_CONTACTINFO',
    'USER_ADDRESS',
    'PLAINTEXT_MOBILE_PHONE'
  ];
  const checkInvalidScopes = (scope: string) => {
    return validScopes.indexOf(`${scope}`.toUpperCase()) === -1;
  };
  return scopes.filter(checkInvalidScopes);
}

export function requiredParamsCheck(
  requiredParams: IAnyStrObject,
  params: IAnyStrObject,
  apiName: string
) {
  for (const [key, value] of Object.entries(requiredParams)) {
    // 仅支持null、undefined及过滤后空字符串的校验
    if (!value || (typeof value === 'string' && value.trim().length === 0)) {
      params.invalidParamObj = logErrorAndRunFail(
        apiName,
        params,
        `parameter error: ${key} should be required instead of Undefined or empty.`
      );
      return false;
    }
  }
  return true;
}

export function tranArrayBufferToUint8Array(data: ArrayBuffer) {
  return Array.from(new Uint8Array(data));
}
